<template>
    <div >
         <Head title="我的结算" />
        <!-- 详情 -->
        <van-cell
        icon="balance-list-o"
        title="我的订单"
        style="text-align:left"
      />

<van-card
:num="count"
:price="obj.price"
:title="obj.name"
:thumb="obj.img"
 style="text-align:left;"
/>
 
 <van-cell
:title="money"
 id="payfor"
 
>
<button class="btn" @click="onSubmit()"> 确认结算</button>
 </van-cell>

 <van-cell
        icon="records"
        title="我的地址"
        style="text-align:left"
      />

 <Address    ref="info" />
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            obj:{},
            count:"",
            money:"",
          
        }

    },
    created(){
        var goodid=this.$route.query.goodid;
      // window.console.log(goodid)
        this.$axios.post("/vue/getpay",{goodid:goodid}).then(res=>{
            if(res.data.code==200){
                Toast('获取信息成功')
                this.obj=res.data.result.goodInfo;
                this.count=res.data.result.count;
                this.money="合计 :￥"+this.count*this.obj.price+".00"
               //  window.console.log( this.obj)

            }else{
                Toast('获取信息失败')
            }
        })
    },
    methods:{
        onSubmit(){
            var addflag=this.$refs.info.addflag;
           if(addflag){
                    Toast("结算成功");
                    this.$router.push({name:"end"})
           }else{
                Toast("您还没有选择地址")
           }

           
        }
    }

    
}
</script>


<style lang="scss" >
.van-address-list{
    padding-bottom: 0px !important;
    
}

.btn{
    width: 150px;
    height:30px;
  background: #ff464e;
  color: #fff;
  border:none;
  font-size: 15px;
 
 
 
}

#payfor{
   text-align:left;
   color: #ff464e;
   margin-left:10px;
}

</style>